          this.organizations = res.data
        })
    },
    getResult() {
      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?region_id=${this.regions_id}&district_id=${this.districts_id}&&test_id=${this.tests_id}`, {
