            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language': 'uz'
          }
        }
      )
    },

      regIstration() {
      axios.post('https://api-kasb.maktab.uz/api/v1/students/register/', {
        "email": this.email,
        "password": this.password,
        "user_type": this.districts_id,
        "username": "mail",
      })
        .then(() => {
          this.$toast("Muvofaqiyatliy ro'yxatdan o'tildi", {
            timeout: 10000,
            type: "success"
          });
        })
        .catch(error => {
          console.log(error)
          this.$toast("Bu email oldin ro'yxatdan o'tgan", {
            timeout: 10000,
            type: "error"
          });
        })

    },
  },
  watch: {
    page(page) {
      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?page=${page}&&class_id=${this.tests_id}`, {
